<template>
  <div class="viewemployee container">
    <h1>View Employee</h1>
    <p>이름 : {{name}}<p>
    <p>직업 : {{job}}<p>
    <p>글 내용 :</p>
    <textarea class="form-control" rows="3" v-model="textinfo"> </textarea>
    <div class="test">
      <div><p>test</p></div>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'

export default {
  name: 'view-employee',
  data () {
    return {
      employee_id: null,
      name: null,
      textinfo: null,
      job: null
    }
  },
  created () {
    this.fetchData()
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      const settings = {timestampsInSnapshots: true}
      db.settings(settings)
      const emNum = parseInt(this.$route.params.employee_id)
      const dataRef = db.collection('employeement').where('id', '==', emNum)
      dataRef.get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.employee_id = doc.data().id
          this.name = doc.data().name
          this.textinfo = doc.data().textinfo
          this.job = doc.data().job
        })
      }).catch(function (error) {
        console.log('Error getting documents: ', error)
      })
    }
  }
}
</script>
<style lang="scss">
</style>



