<template>
  <div class="dashboard container">
    <h3>Massage List</h3>
    <table class="table table-hover">
      <thead>
        <tr>
          <th>#</th>
          <th>Name</th>
          <th>Massage</th>
          <th>Position</th>
          <th>Detail View</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in employees" v-bind:key="item.employee_id">
          <td><span class="badge">{{item.id}}</span></td><td>{{item.name}}</td><td>{{item.textinfo}}</td><td>{{item.job}}</td>
          <td><router-link v-bind:to="{ name: 'view-employee', params: { employee_id: item.employee_id }}"><span class="glyphicon glyphicon-eye-open" aria-hidden="true"></span></router-link></td>
        </tr>
      </tbody>
    </table>
    <button type="button" class="btn btn-default btn-lg pull-right"><router-link to="/new">wirte</router-link></button>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data () {
    return {
      employees: []
    }
  },
  created () {
    const settings = {timestampsInSnapshots: true}
    db.settings(settings)
    db.collection('employeement').get().then(querySnapshot => {
      let idNum = 0
      querySnapshot.forEach(doc => {
        idNum += 1
        const data = {
          'id': idNum,
          'employee_id': doc.data().id,
          'name': doc.data().name,
          'textinfo': doc.data().textinfo,
          'job': doc.data().job
        }
        this.employees.push(data)
      })
    })
  }
}
</script>

<style lang="scss">
.dashboard table th,.dashboard table{text-align:center;}
</style>



